<template>
    <view class="transform_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">转化</block></cu-custom>
        <view class="things_width_full flex justify-between align-center transform_page_top">
            <view class="current_points">当前营利分 <text class="points_value">{{userAmount.profit_points}}</text> </view>
            <view class="rule">交易规则</view>
        </view>
        <view class="flex justify-center align-start input_value_box">
            <view class="input_value_title">转化为分红金</view>
            <view class="things_width_full flex justify-between align-center form_box">
                <view class="flex justify-start align-center input_box">
                    <input class="withdraw_money" type="digit" placeholder="输入积分" :value="profit_points" @input="profitPointsInput" />
                </view>
                <button class="cu-btn submit_btn" :disabled="buttonType" @tap="submitFun">确定</button>
            </view>
            <view class="rule_tip">这里是交易提示说明</view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { giftsPointsFraction_api,profitTurnBonus_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
let that = null;
export default {
    data() {
        return {
            // 用户数据
            userData: storage.get("userData") || {},
            profit_points: '',// 输入的营利分
            userAmount: {},// 积分分数
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.getFraction();
        },
        /**提交表单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        submitFun(e) {
            if (!that.profit_points || Number(that.profit_points) === 0) {
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请输入转化数量'}});
                return false;
            };
            that.buttonType = true;
            that.modal({name:'popLoading',value:true});
            let params = {
                profit_points: that.profit_points
            };
            profitTurnBonus_api(params,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.buttonType = false;
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'转化成功'}});
                that.clearPageFun();
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
                that.modal({name:'popLoading',value:false});
            })
        },
        /**营利分输入
         * @description: 
         * @param {*} e
         * @return {*}
         */
        profitPointsInput(e) {
            that.profit_points = e.detail.value;
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>